import React, { useState, useEffect } from 'react';
import logo from './logo.svg';
import './App.css';
import tipJarData from './data/tipjardata.json'
import tipJarLocationData from './data/locationData.js'
import TipJarNav from './components/TipJarNav'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Card from 'react-bootstrap/Card'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'

function App() {
  const [ peopleData, setPeopleData] = useState([])
  const [ locationData, setLocationData ] = useState([])
  const [ searchParam, setSearchParam ] = useState('')
  const [ searchResults, setSearchResults ] = useState([])

  useEffect(() => {
    setPeopleData(tipJarData)
    setLocationData(tipJarLocationData)
   }, [])

  useEffect(() => {
    if (searchParam === ''){
      setSearchResults(peopleData)
    } else {
      const results = peopleData.filter(function(e){
        return e.Name.toLowerCase().startsWith(searchParam.toLowerCase())
      })
      setSearchResults(results)
    }
  }, [searchParam]);

  const handleSearchChange = (event) => {
    setSearchParam(event.target.value)
  }

  const generateVenmoLink = (venmoId) => {
    return `https://venmo.com/${venmoId}`
  }



  return (
    <div className="App">
      <TipJarNav/>
      <Container>
        <Jumbotron className='mt-4'>
          <h1>Philly Tip Jar</h1>
          <p>
            Here to help the thousands of service workers furloughed because of COVID-19.
          </p>
          <p>
            <Button variant="primary">Learn more</Button>
          </p>
        </Jumbotron>
        <Form >
          <Form.Label>start typing to search</Form.Label>
          <Form.Control
            onChange={(e) => handleSearchChange(e)}
            value={searchParam}
            type="text"
            placeholder="search"
            />
        </Form>
        {searchResults.map((person, index) => {
          if (index <= 20){
            return (
              <Row>
                <Col className='col-sm-8 col-md-6'>
                  <Card key={person.id} style={{ margin: '2rem 2rem' }}>
                    <Card.Body>
                      <Card.Title>{person.Name}</Card.Title>
                      <Card.Subtitle className="mb-2 text-muted">example role</Card.Subtitle>
                      <Card.Text>
                        {person.Name} works at {person.Location}.
                        {person.Healthcare === 'No' ? ` They do not currently have healthcare` : null }
                      </Card.Text>
                      <Card.Link href={generateVenmoLink(person.Venmo)}>Venmo</Card.Link>
                      <Card.Link href="#">paypal</Card.Link>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            )
          } else {
            return
          }
          })}
      </Container>
    </div>
  );
}

export default App;
